import React, { Component } from 'react';
import './App.css';
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { getEvents } from './api';
import {OfflineAlert} from './Alert';
import moment from 'moment';
import {
    ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, PieChart, Pie, Legend
} from 'recharts';

class App extends Component {

  state = {
      lat: null,
      lon: null,
      page: '',
      events: []
  };

  componentDidMount() {
      this.updateEvents();

      window.addEventListener('offline', () => this.setState({offlineText: 'Displaying cached list of events while user is offline'}));
      window.addEventListener('online', () => this.setState({offlineText: ''}));
  }

    countEventsOnADate = (date) => {
        let count = 0;
        for (let i = 0; i < this.state.events.length; i += 1) {
            if (this.state.events[i].local_date === date) {
                count += 1;
            }
        }
        return count;
    }

    getData = () => {
        const next7Days = []; // Create empty array for the next 7 days
        const currentDate = moment(); // Today
        // Loop 7 times for next 7 days
        for (let i = 0; i < 7; i += 1) {
            currentDate.add(1, 'days'); // Add 1 day to current date, currentDate changes
            const dateString = currentDate.format('YYYY-MM-DD'); // Format the date
            // Use the countEventsOnADate function to count #events on this date
            const count = this.countEventsOnADate(dateString);
            next7Days.push({ date: dateString, number: count }); // Add this date and number to the list
        }
        return next7Days;
    }

  changePage(value) {
      this.setState({
          page: value
      });

      this.updateEvents(this.state.lat, this.state.lon, value);
  }

  updateEvents = (lat, lon, page) => {

      getEvents(
          lat ? lat : this.state.lat,
          lon ? lon : this.state.lon,
          (page !== undefined) ? page : this.state.page).then(events => {
              this.setState({events, lat, lon});
          }
      );
  }

  render() {
    return (
      <div className="App">
          <h1>Meetup Events</h1>
          <OfflineAlert text={this.state.offlineText} />
          <CitySearch updateEvents={this.updateEvents} />
          <NumberOfEvents
              page={this.state.page}
              changePage={this.changePage.bind(this)} />
          <ResponsiveContainer className="graph" height={400}>
              <ScatterChart margin={{ top: 20, right: 20, bottom: 10, left: 10 }}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis type="category" dataKey="date" name="date" />
              <YAxis type="number" dataKey="number" name="number of events" allowDecimals={false} />
              <Tooltip cursor={{ strokeDasharray: '3 3' }} />
              <Scatter data={this.getData()} fill="#8884d8" />
              </ScatterChart>
          </ResponsiveContainer>
          <EventList
              events={this.state.events}
              page={this.state.page} />
      </div>
    );
  }
}

export default App;
