import React, { Component } from 'react';
import './App.css';
import './Event.css';
import {
    ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, PieChart, Pie, Legend
} from 'recharts';

class Event extends Component {
    state = {
        showDetails: false
    };

    expandCollapseDetails = () => {
        if(this.state.showDetails === false) {
            this.setState({showDetails: true});
        }
        else {
            this.setState({showDetails: false});
        }
    }


    render() {
        if (!this.props.event) return;
        //const displayDetails = this.state.displayDetails;

        let venueInformation = '';
        if (this.props.event.venue && this.props.event.venue.name) {
            venueInformation += this.props.event.venue.name;
        }
        if (this.props.event.venue && this.props.event.venue.address_1) {
            venueInformation += (venueInformation.length ? ' - ' : '') + this.props.event.venue.address_1;
        }

        let pieData=[
            {name: 'Reservations', value: this.props.event.yes_rsvp_count},
            {name: 'Free slots', value: this.props.event.rsvp_limit - this.props.event.yes_rsvp_count }
        ];

        let pie;

        if (this.props.event.rsvp_limit) {
            pie = (
                <ResponsiveContainer height={400} className="Pie">
                    <PieChart width={400} height={400}>
                        <Pie dataKey="value" isAnimationActive={false} data={pieData} cy={200} outerRadius={80} fill="#29AB87" label />
                        <Tooltip />
                    </PieChart>
                </ResponsiveContainer>
            )
        }

        let details;
        if (this.state.showDetails) {
            details = (<span>
                        {pie}
                        <li>{venueInformation}</li>
                        <li>{this.props.event.group.localized_location}</li>
                        <li dangerouslySetInnerHTML={{ __html: this.props.event.description }}></li>
                        <li className="visibility">{this.props.event.visibility}</li>
                        <li><a href="https://www.meetup.com/de-DE/jug-hamburg/events/263370107/" target="_self" aria-label="Learn more about the event">Event link</a></li>
            </span>);
        }
        return (
            <div className="overview">
                <ul className="Event">
                    <li className="date_time">
                        {this.props.event.local_time} - {this.props.event.local_date}
                    </li>
                    <li className="event_name">
                        {this.props.event.name}
                    </li>
                    <li className={"group_name"}>
                        GROUP: {this.props.event.group ? this.props.event.group.name : ""}
                    </li>
                    <li className="event_rsvp">
                        {this.props.event.yes_rsvp_count} are going
                    </li>
                    {details}
                    <button className="more_details" label="Details" onClick={() => this.expandCollapseDetails()}>Details</button>
                </ul>
            </div>
        );
    }

}

export default Event;
